@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap");
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

body {
  color: #4e5860;
  background-color: #fff;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif; }

.text_input {
  margin-top: 20px; }
  .text_input input {
    font-size: 16px;
    font-weight: 600;
    width: 50%;
    height: 40px;
    padding-left: 8px;
    border: none;
    /* <-- This thing here */
    border: solid 1px #ccc;
    border-radius: 3px; }
  .text_input input:focus {
    outline: none; }

.area_input {
  margin-top: 20px;
  width: 100%; }
  .area_input textarea {
    padding: 8px 8px 8px 8px;
    width: 100%;
    height: 500px;
    resize: none;
    outline: none; }

.submit_button {
  margin-top: 20px; }
  .submit_button input[type="submit"] {
    width: 100%;
    height: 40px;
    background-color: #2c3e50;
    border: none;
    /* <-- This thing here */
    border-radius: 3px;
    color: white;
    font-size: 18px;
    font-weight: 600;
    transition: 0.2s ease-in-out; }
  .submit_button :hover {
    cursor: pointer; }
  .submit_button input[type="submit"]:hover {
    background-color: rgba(44, 62, 80, 0.883);
    transition: 0.2s ease-in-out; }

.form {
  padding: 6px 15px 15px 15px;
  background-color: #f1f5fc8e;
  border-radius: 3px; }
