@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap");
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

body {
  color: #4e5860;
  background-color: #fff;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif; }

.nav {
  padding: 4px 5% 0px 5%;
  display: grid;
  grid-template-columns: 10fr 9fr 10fr;
  grid-column-gap: 0px;
  height: 80px;
  width: 100%;
  font-size: 30px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.15);
  background: #fff;
  border-bottom: 2px solid rgba(0, 0, 0, 0.055);
  color: #2c3e50; }
  @media (max-width: 840px) {
    .nav {
      grid-template-columns: 0fr 1fr 0fr; } }
  .nav h1 {
    font-size: 25px;
    line-height: 0.9; }
  .nav h2 {
    font-size: 20px;
    font-weight: 400;
    line-height: 0.9; }
  .nav .besedilapesmi {
    margin-top: 10px; }
  .nav .akordichords {
    display: flex; }
  .nav .chords {
    margin-top: 4px;
    margin-left: 2px; }

.logo {
  background-image: url("/static/notes.png");
  background-repeat: no-repeat;
  background-position-x: 167px;
  background-position-y: 33px;
  background-size: 92px; }
  @media (max-width: 840px) {
    .logo {
      width: 0;
      visibility: hidden; } }

.searchbar {
  display: grid;
  width: 100%;
  grid-template-columns: 10fr 1fr;
  height: 46px;
  margin-top: 11px;
  line-height: 1;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.15);
  margin-left: auto;
  margin-right: auto;
  border: 1px solid rgba(128, 128, 128, 0.76);
  border-radius: 3px;
  background-color: white;
  color: rgba(44, 42, 42, 0.9);
  font-size: 24px;
  align-content: center; }
  .searchbar input {
    width: 100%;
    /* force to expand to container's width */
    border: none;
    background-color: transparent;
    font-size: 18px;
    padding: 0px 8px 4px 8px; }
  .searchbar input::placeholder {
    color: rgba(0, 0, 0, 0.3);
    font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
    font-weight: 400; }
  .searchbar input:focus {
    outline: none; }
  .searchbar .searchicon {
    margin-top: 4px;
    text-align: right;
    margin-right: 10px; }
    .searchbar .searchicon img {
      height: 22px; }
    .searchbar .searchicon img:hover {
      opacity: 0.6;
      cursor: pointer; }

.right_menu {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  text-align: right;
  margin-bottom: 13px;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.2; }
  .right_menu a {
    text-decoration: none;
    color: #2c3e50; }
  @media (max-width: 840px) {
    .right_menu {
      width: 0;
      visibility: hidden; } }
